<template>
  <div id="Emergency">
    <el-row :gutter="12">
      <el-col :span="12">
        <el-card shadow="hover">
          <span><b>Client Details</b></span>
          <div id="detail">
            <div id="client"><img src="/icon_client.4ef2f17c.png"></div>
            <div id="name">
              <span class="nametip">FIRST NAME</span>
              <span class="namespan">{{first_name}}</span>
              <span class="nametip">LAST NAME</span>
              <span class="namespan">{{last_name}}</span>
            </div>
          </div>
          <div id="note">
            <span>NOTES</span>
            <el-button @click="edit_note" type="text" v-if="note_edit_type == false" id="edit_button">EDIT</el-button>
            <el-input 
            v-show="note_edit_type == false"
            autosize
            type="textarea"
            v-model="note"
            readonly="readonly"
            class="show_text">
            </el-input>
            <el-input
            v-if="note_edit_type == true"
            type="textarea"
            :rows="8"
            placeholder=""
            v-model="note"
            >
            </el-input>
            <el-button @click="sendNote" v-if="note_edit_type == true" id="done_button">
              DONE
            </el-button>
          </div>
        </el-card>
      </el-col>
      <el-col :span="12">
        <el-card shadow="hover">
          <div id="title">
            <span><b>Safety Plan</b></span>
            <el-button @click="edit_plan" type="text" v-if="plan_edit_type == false" id="edit_button">EDIT</el-button>
          </div>
          <div id="content">
            <el-input 
            v-show="plan_edit_type == false"
            autosize
            type="textarea"
            v-model="textarea"
            readonly="readonly"
            class="show_text">
            </el-input>
            <el-input
            v-if="plan_edit_type == true"
            type="textarea"
            :rows="8"
            placeholder=""
            v-model="textarea"
            >
            </el-input>
            <el-button @click="sendEmergency" v-if="plan_edit_type == true" id="done_button">
              DONE
            </el-button>
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  import axios from 'axios'
export default {
  data() {
    return {
      textarea: '',
      note:'',
      plan_edit_type: false,
      note_edit_type: false,
      new_emergency: false,
      first_name: '',
      last_name: '',
    }
  },
  created() {
    axios.get('http://35.197.160.94:8094/user/getNote', {
      params: {
        uid: this.$store.state.patient_id
      }
    })
    .then((response) => {
      const raw = response.data
      this.note = raw.note
      this.first_name = raw.firstName
      this.last_name = raw.lastName
    })
    axios.get('http://35.197.160.94:8094/user/getEmergency', {
      params: {
        uid: this.$store.state.patient_id
      }
    })
    .then((response) => {
      const raw = response.data
      this.textarea = raw.emergency
      if (this.textarea == null) {
        this.new_emergency = true
      }
    })
  },
  methods: {
    edit_plan() {
      this.plan_edit_type = true
    },
    edit_note() {
      this.note_edit_type = true
    },
    sendNote() {
      axios.post('http://35.197.160.94:8094/user/updateNote', {
        uid: this.$store.state.patient_id,
        birth: this.note
      })
      .then((response) => {
          if (response.data == '1'){
            this.note_edit_type = false
            this.$message({
              type: 'success',
              message: 'Update Note Successed'
            });
          }
        })
        .catch((error) => {
          console.log(error);
          this.$message({
            message: "Network Error",
            type: "error"
          })
      })
    },
    sendEmergency() {
      if (this.new_emergency) {
        axios.get('http://35.197.160.94:8094/user/addEmergency', {
          params: {
            uid: this.$store.state.patient_id,
            emergency: this.textarea,
          }
        })
        .then((response) => {
          if (response.data == '1'){
            this.plan_edit_type = false
            this.$message({
              type: 'success',
              message: 'Add Emergency Information Successed'
            });
          }
        })
        .catch((error) => {
          console.log(error);
          this.$message({
            message: "Network Error",
            type: "error"
          })
      })
    } else {
      axios.get('http://35.197.160.94:8094/user/updateEmergency', {
          params: {
            uid: this.$store.state.patient_id,
            emergency: this.textarea,
          }
        })
        .then((response) => {
          if (response.data == '1'){
            this.plan_edit_type = false
            this.$message({
              type: 'success',
              message: 'Update Emergency Information Successed'
            });
          }
        })
        .catch((error) => {
          console.log(error);
          this.$message({
            message: "Network Error",
            type: "error"
          })
      })
    }
    }
  }
}
</script>

